import React, { Component } from "react";
import axios from "axios";
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import RadioButton from './RadioButton.js';
class PopBooks extends Component {
  constructor(props) {
    super(props);
    this.state = {
      info: []
    };

    this.getPopularBooks = this.getPopularBooks.bind(this);
  }

  getPopularBooks() {
    axios
      .post("http://localhost:3001/proc/getPopularBooks", { page: 1 })
      .then(res => {
        console.log(res.data);
        this.setState({ info: res.data });
      })
      .catch(err => {
        console.log("error occurred while getting popular books");
      });
  }

  render() {
    const StyledButton = withStyles({
      root: {
        height:56,
        marginLeft:'1%',
        alignContent:'center',
        width:'10%',
        background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
        borderRadius: 3,
        border: 0,
        color: 'white',
        boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
      },
      label: {
        textTransform: 'capitalize',
      },
    })(Button);
    const TextButton = withStyles({
      root: {
        height:56,
        alignContent:'center',
        width:'100%',
        background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
        borderRadius: 3,
        border: 0,
        color: 'white',
      },
      label: {
        textTransform: 'capitalize',
      },
    })(Button);
    return (
      <div>
        <TextButton onClick={this.getPopularBooks}>Gauti populiarias knygas</TextButton>
        {this.state.info.map((knyga, index) => {
          return (
            <div key={index}>
              <h2>
                <b>Knygos pavadinimas: </b>
                <u>{knyga.pavad}</u>
                <b> Autorius: </b>
                <u>{knyga.vard}</u>
              </h2>
              <h2>
                <b>Leidykla: </b>
                {knyga.leidykla} <br />
                <b> Kiek kartu paimta: </b>
                {knyga.paemimai}
              </h2>
            </div>
          );
        })}
      </div>
    );
  }
}

//isbn, pavad, bukle, leidykla, leid_met, paras_met, psl, kalba, isd_laik, prates_kart, SUM(pk) AS paemimai, vard
export default PopBooks;
