import React, { Component } from "react";

import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
class RadioButton extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedOption: "1"
    };
  }

  handleOptionChange = changeEvent => {
    this.setState({
      selectedOption: changeEvent.target.value
    });
  };

  handleFormSubmit = formSubmitEvent => {
    formSubmitEvent.preventDefault();

    this.props.handleFilter(this.state.selectedOption); 
  };

  render() {
    const StyledButton = withStyles({
      root: {
        height:56,
        marginLeft:'1%',
        alignContent:'center',
        width:'10%',
        background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
        borderRadius: 3,
        border: 0,
        color: 'white',
        boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
      },
      label: {
        textTransform: 'capitalize',
      },
    })(Button);
    const TextButton = withStyles({
      root: {
        height:56,
        alignContent:'center',
        width:'100%',
        background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
        borderRadius: 3,
        border: 0,
        color: 'white',
      },
      label: {
        textTransform: 'capitalize',
      },
    })(Button);



    return (
      <div className="container">
        <div className="row mt-5">
          <div className="col-sm-12">
            <form onSubmit={this.handleFormSubmit}>
            <ul>Knygų kalba</ul>
              <div className="form-check">
                <label>
                  <input
                    type="radio"
                    name="react-tips"
                    value="1"
                    checked={this.state.selectedOption === "1"}
                    onChange={this.handleOptionChange}
                    className="form-check-input"
                  />
                  LT
                </label>
              </div>
              <div className="form-check">
                <label>
                  <input
                    type="radio"
                    name="react-tips"
                    value="2"
                    checked={this.state.selectedOption === "2"}
                    onChange={this.handleOptionChange}
                    className="form-check-input"
                  />
                  EN
                </label>
              </div>
              <div className="form-check">
                <label>
                  <input
                    type="radio"
                    name="react-tips"
                    value="3"
                    checked={this.state.selectedOption === "3"}
                    onChange={this.handleOptionChange}
                    className="form-check-input"
                  />
                  RU
                </label>
              </div>
              <div className="form-check">
                <label>
                  <input
                    type="radio"
                    name="react-tips"
                    value="4"
                    checked={this.state.selectedOption === "4"}
                    onChange={this.handleOptionChange}
                    className="form-check-input"
                  />
                  PL
                </label>
              </div>
              <div className="form-check">
                <label>
                  <input
                    type="radio"
                    name="react-tips"
                    value="5"
                    checked={this.state.selectedOption === "5"}
                    onChange={this.handleOptionChange}
                    className="form-check-input"
                  />
                  Latin
                </label>
              </div>
              <div className="form-check">
                <label>
                  <input
                    type="radio"
                    name="react-tips"
                    value="6"
                    checked={this.state.selectedOption === "6"}
                    onChange={this.handleOptionChange}
                    className="form-check-input"
                  />
                  GER
                </label>
              </div>
              <div className="form-check">
                <label>
                  <input
                    type="radio"
                    name="react-tips"
                    value="7"
                    checked={this.state.selectedOption === "7"}
                    onChange={this.handleOptionChange}
                    className="form-check-input"
                  />
                  FR
                </label>
              </div>
              
              <div className="form-check">
                <label>
                  <input
                    type="radio"
                    name="react-tips"
                    value="8"
                    checked={this.state.selectedOption === "8"}
                    onChange={this.handleOptionChange}
                    className="form-check-input"
                  />
                  ES
                </label>
              </div>
              <div className="form-group">
                <StyledButton className="btn btn-primary mt-2" type="submit">
                  Save
                </StyledButton>
              </div>
            </form>
          </div>
        </div>
      </div>
    );
  }
}

export default RadioButton;