import React, { Component } from 'react';
import './App.css';
import Input from './Input.js';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import RadioButton from './RadioButton.js';
import axios from 'axios';
import PopBooks from './PopBooks';
class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      view:0,      // 0- search and book display 1 - user's books
      search:
      {
      string:"",
      page:1,
      srch_num:"10",
      psl_low:-1,
      psl_high:-1,
      lang:-1,
      uzimt:-1,
      met_low:-1,
      met_high:-1,
      aut:-1
      },
      results:
      [ 
      ],
      taken:
      [
      ],
       open: false,
    }
  }
sendrequest=()=>{
  console.log(this.state.search);
  axios.post('http://localhost:3001/proc/searchBooks',this.state.search)
  .then( (response)=> {
    this.setState({results:response.data});
    console.log(response.data);
  })
  .catch((error)=> {
    console.log(error);
  });
}
sendanotherrequest=()=>{
  axios.get('http://localhost:3001/proc/getTakenBoks')
  .then( (response)=> {
    this.setState({taken:response.data});
    console.log(response.data);
  })
  .catch((error)=> {
    console.log(error);
  });
}
logout=()=>{
  axios.get('http://localhost:3001/proc/logout')
  .then( (response)=> {
    window.location = "/public/";
  })
  .catch((error)=> {
    console.log(error);
  });
}
handleChange = (input) => {
this.state.search.string=input.target.value;
this.forceUpdate();
}
  handleButtonClick = () => {
    this.setState(state => {
      return {
        open: !state.open,  
      };
    });
  }
  userClick =()=>
  {
    if(this.state.view==0)
    this.state.view=1;
    else
    this.state.view=0;
    this.forceUpdate();
  }
  PopBoks =()=>
  {
    this.state.view=2;
    this.forceUpdate();
  }
handleFilter = (language) => {
  
this.state.search.lang=language;
this.forceUpdate();
}
handleInput = (input) =>
{
  this.state.search.string=input;
  this.forceUpdate();
}
nextPage = () =>
{
  this.state.search.page++;
  this.forceUpdate();
}
previousPage = () =>
{
  if(this.state.search.page>1)
  this.state.search.page--;
  this.forceUpdate();
}

  render() {
    switch (this.state.view) {
      case 0:{
        const StyledButton = withStyles({
          root: {
            height:56,
            marginLeft:'1%',
            alignContent:'center',
            width:'10%',
            background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
            borderRadius: 3,
            border: 0,
            color: 'white',
            boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
          },
          label: {
            textTransform: 'capitalize',
          },
        })(Button);
        const TextButton = withStyles({
          root: {
            height:56,
            alignContent:'center',
            width:'100%',
            background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
            borderRadius: 3,
            border: 0,
            color: 'white',
          },
          label: {
            textTransform: 'capitalize',
          },
        })(Button);
    return (
      <div>
      <div className={"SearchBar"}>
       <Input handleInput={this.handleInput}></Input>
       <StyledButton onClick={this.sendrequest} >Ieškoti</StyledButton>
       <div className={"container"}>
       
       <StyledButton onClick={this.handleButtonClick} className={"filterButton"}>Filtruoti</StyledButton>
       {this.state.open && (
       <div className={"dropdownButton"}><ul>
      <RadioButton
       className={"dropdown"}
       handleFilter={this.handleFilter}></RadioButton>
       </ul></div>
       )}
       </div>
      </div>
      <div className={"inline"}>
      <div className={"userButton"}> 
      <TextButton onClick={this.userClick}>Mano knygos</TextButton>
      <TextButton onClick={this.PopBoks}>Populiarios knygos</TextButton>
      <TextButton onClick={this.logout}>Atsijungti</TextButton>
      <div><h2  className={"puslapiai"}>Dabartinis puslapis: {this.state.search.page}</h2><h2  className={"puslapiai"} onClick={this.nextPage} >Kitas puslapis</h2><h2 className={"puslapiai"} onClick={this.previousPage}>Praeitas puslapis</h2></div>
      </div>
      <div className={"divccss"}>      
      {
        this.state.results.map((rezultatai,index)=>{
          return(
          <div key={index}className={"knyga"}>
          <h1>Pavadinimas | {rezultatai.pavad}</h1>
          <h1>ISBN | {rezultatai.isbn}</h1>
          <h1>Būklė | {rezultatai.bukle}</h1>
          <h1>Autorius | {rezultatai.vard}</h1>
          <h1>Leidimo m. | {rezultatai.leid_met}</h1>
          <h1>Parašymo m. | {rezultatai.paras_met}</h1>
          <h1>Kalba | {rezultatai.kalba}</h1>
          
          <h1>Psl. sk. | {rezultatai.psl}</h1>
          <h1>Išdavimo laikas | {rezultatai.isd_laik}</h1>
          <h1>Pratęsimo sk. | {rezultatai.prates_kart}</h1>
          
          
          </div>);
        })
      }
      </div>
      </div>
      </div>
    );
  }
    case 1:{
      const StyledButton = withStyles({
        root: {
          height:56,
          marginLeft:'1%',
          alignContent:'center',
          width:'10%',
          background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
          borderRadius: 3,
          border: 0,
          color: 'white',
          boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
        },
        label: {
          textTransform: 'capitalize',
        },
      })(Button);
      const TextButton = withStyles({
        root: {
          height:56,
          alignContent:'center',
          width:'100%',
          background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
          borderRadius: 3,
          border: 0,
          color: 'white',
        },
        label: {
          textTransform: 'capitalize',
        },
      })(Button);
      return (
       
        <div>
        <TextButton onClick={this.userClick}>Grįžti</TextButton>
        <TextButton onClick={this.sendanotherrequest}>Gauti paimtas knygas</TextButton>
        <div className={"manoknygos"}>      
      {
        this.state.taken.map((rezultatai,index)=>{
          return(
          <div key={index}className={"knyga"}>
          <h1>ISBN | {rezultatai.info.isbn}</h1>
          <h1>Pavadinimas | {rezultatai.info.pavad}</h1>
          <h1>Autorius | {rezultatai.info.vard}</h1>
          <h1>Leidykla | {rezultatai.info.leidykla}</h1>          
          <h1>Paėmimo data | {rezultatai.p_data.slice(0,10)}</h1>          
          
          </div>);
        })
      }
      </div>
        </div>

      );
    }
    case 2:{
      const TextButton = withStyles({
        root: {
          height:56,
          alignContent:'center',
          width:'100%',
          background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
          borderRadius: 3,
          border: 0,
          color: 'white',
        },
        label: {
          textTransform: 'capitalize',
        },
      })(Button);
      return(
    <div><TextButton onClick={this.userClick}>Grįžti</TextButton>
    <PopBooks></PopBooks></div>
    
      );
  }
}
}
}

export default App;
